.the-box div {
  box-sizing: border-box;
  border: solid black 5px;
}

.app-container .houses {
  gap: 70px;
}

.the-box.in .front-left-lid {
  animation: front-left-lid-open 1s ease-in-out 0s 1 normal forwards;
}

.the-box.in .back-right-lid {
  animation: back-right-lid-open 1s ease-in-out 0s 1 normal forwards;
}

.the-box.in .back-left-lid {
  animation: back-left-lid-open 1s ease-in-out 0s 1 normal forwards;
}

.the-box.in .front-right-lid {
  animation: front-right-lid-open 1s ease-in-out 0s 1 normal forwards;
}

.the-box.infrontof .front-left-lid {
  animation: front-left-lid-open 1s ease-in-out 0s 1 normal forwards;
}

.the-box.infrontof .back-right-lid {
  animation: back-right-lid-open 1s ease-in-out 0s 1 normal forwards;
}

.the-box.infrontof .back-left-lid {
  animation: back-left-lid-open 1s ease-in-out 0s 1 normal forwards;
}

.the-box.infrontof .front-right-lid {
  animation: front-right-lid-open 1s ease-in-out 0s 1 normal forwards;
}


.the-box.near .front-left-lid {
  animation: front-left-lid-open 1s ease-in-out 0s 1 normal forwards;
}

.the-box.near .back-right-lid {
  animation: back-right-lid-open 1s ease-in-out 0s 1 normal forwards;
}

.the-box.near .back-left-lid {
  animation: back-left-lid-open 1s ease-in-out 0s 1 normal forwards;
}

.the-box.near .front-right-lid {
  animation: front-right-lid-open 1s ease-in-out 0s 1 normal forwards;
}

.the-box div.animal {
  transition-duration: 1s;
  transform: rotate3d(0, 1, 0, -50deg) translate3D(18px, 10px, -0);
  position: absolute;
  border: 0;
  font-size: 50px;
}

.the-box div.animal.in {
  transform: rotate3d(0, 1, 0, -50deg) translate3D(16px, -47px, -0);
}

.the-box div.animal.on {
  animation: animalon 1s ease-in-out 0s 1 normal forwards;
}

.the-box div.animal.above {
  animation: animalabove 1s ease-in-out 0s 1 normal forwards;
}

.the-box div.animal.under {
  animation: animalunder 1s ease-in-out 0s 1 normal forwards;
}

.the-box div.animal.infrontof {
  animation: animalinfrontof 1s ease-in-out 0s 1 normal forwards;
}

.the-box div.animal.behind {
  animation: animalbehind 1s ease-in-out 0s 1 normal forwards;
}
.the-box div.animal.near {
  animation: animalnear 1s ease-in-out 0s 1 normal forwards;
}

.the-box.under {
  animation: boxunder 1s ease-in-out 0s 1 normal forwards;
  /* transform: rotate3d(1, 0, 0, -190deg); */
}

.house-container.near {
  z-index: 1;
}

@keyframes boxunder {
  from {
    transform: rotate3d(1, 0, 0, -23deg) rotate3d(0, 1, 0, 50deg)
    translate3D(50px, 25px, -50px);
  }
  to {
    transform: rotate3d(1, 0, 0, -23deg) rotate3d(0, 1, 0, 50deg)
    translate3D(50px, -23px, -50px);
  }
}

@keyframes animalon {
  from {
    transform: rotate3d(0, 1, 0, -50deg) translate3D(16px, -166px, -0);
  }
  to {
    transform: rotate3d(0, 1, 0, -50deg) translate3D(16px, -61px, -0);
  }
}

@keyframes animalabove {
  from {
    transform: rotate3d(0, 1, 0, -50deg) translate3D(16px, -166px, -0);
  }
  to {
    transform: rotate3d(0, 1, 0, -50deg) translate3D(16px, -100px, -0);
  }
}

@keyframes animalunder {
  from {
    transform: rotate3d(0, 1, 0, -50deg) translate3D(18px, 10px, -0);
  }
  to {
    transform: rotate3d(0, 1, 0, -50deg) translate3D(16px, 100px, -0);
  }
}
@keyframes animalinfrontof {
  from {
    transform: rotate3d(0, 1, 0, -50deg) translate3D(16px, 10px, 0);
  }
  50% {
    transform: rotate3d(0, 1, 0, -50deg) translate3D(13px, -120px, 70px);
  }
  to {
    font-size: 75px;
    transform: rotate3d(0, 1, 0, -50deg) translate3D(7px, 10px, 90px);
  }
}
@keyframes animalnear {
  from {
    transform: rotate3d(0, 1, 0, -50deg) translate3D(16px, 10px, 0);
  }

  50% {
    transform: rotate3d(0, 1, 0, -50deg) translate3D(30px, -140px, 50px);
  }
  to {
    font-size: 70px;
    transform: rotate3d(0, 1, 0, -50deg) translate3D(82px, -14px, 100px);
  }
  /* to {
    font-size: 65px;
    transform: rotate3d(0, 1, 0, -50deg) translate3D(71px, 20px, 70px);
  } */
}
@keyframes animalbehind {
  from {
    transform: rotate3d(0, 1, 0, -50deg) translate3D(16px, 10px, -70px);
  }
  to {
    transform: rotate3d(0, 1, 0, -50deg) translate3D(-57px, -26px, -70px);
  }
}

.box-container {
  width: 100px;
  height: 100px;
  margin: 50px auto;
  /* -webkit-perspective: 1500px;
  -moz-perspective: 1500px;
  -ms-perspective: 1500px;
  perspective: 1500px; */
