  animation: cRMhPG 0.7s;
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s; */
}

.the-box {
  position: relative;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transform: rotate3d(1, 0, 0, -23deg) rotate3d(0, 1, 0, 50deg)
    translate3D(50px, 25px, -50px);
}


/* fragile label*/
.front-right-side {
  position: absolute;
  width: 100%;
  height: 100%;
  /* background: orange; */
  -webkit-transform: translate3d(0, 0, 50px);
  -ms-transform: translate3d(0, 0, 50px);
  transform: translate3d(0, 0, 50px);
}

.back-left-side {
  position: absolute;
  width: 100%;
  height: 100%;
  /* background: orange; */
  -webkit-transform: rotate3d(0, 1, 0, 180deg) translate3d(0, 0, 50px);
  -ms-transform: rotate3d(0, 1, 0, 180deg) translate3d(0, 0, 50px);
  transform: rotate3d(0, 1, 0, 180deg) translate3d(0, 0, 50px);
}

.front-left-side {
  position: absolute;
  width: 100%;
  height: 100%;
  /* background: orange; */
  -webkit-transform: rotate3d(0, 1, 0, -90deg) translate3d(0, 0, 50px);
  -ms-transform: rotate3d(0, 1, 0, -90deg) translate3d(0, 0, 50px);
  transform: rotate3d(0, 1, 0, -90deg) translate3d(0, 0, 50px);
}

.back-right-side {
  position: absolute;
  width: 100%;
  height: 100%;
  /* background: orange; */
  -webkit-transform: rotate3d(0, 1, 0, 90deg) translate3d(0, 0, 50px);
  -ms-transform: rotate3d(0, 1, 0, 90deg) translate3d(0, 0, 50px);
  transform: rotate3d(0, 1, 0, 90deg) translate3d(0, 0, 50px);
}

.box-bottom {
  position: absolute;
  width: 100%;
  height: 100%;
  /* background: orange; */
  -webkit-transform: rotate3d(1, 0, 0, -90deg) translate3d(0, 0, 50px);
  -ms-transform: rotate3d(1, 0, 0, -90deg) translate3d(0, 0, 50px);
  transform: rotate3d(1, 0, 0, -90deg) translate3d(0, 0, 50px);
}

.lids {
  position: absolute;
  width: 100%;
  height: 100%;
  /* background: orange; */
  -webkit-transform-style: preserve-3d;
  -ms-transform-style: preserve-3d;
  transform-style: preserve-3d;
  background: transparent;
  border: none;
  -webkit-transform: rotate3d(1, 0, 0, 90deg) translate3d(0, 0, 50px);
  -ms-transform: rotate3d(1, 0, 0, 90deg) translate3d(0, 0, 50px);
  transform: rotate3d(1, 0, 0, 90deg) translate3d(0, 0, 50px);
}

.front-left-lid {
  position: absolute;
  width: 50%;
  height: 100%;
  /* background: orange; */
  -webkit-transform-origin: left center;
  -ms-transform-origin: left center;
  transform-origin: left center;
}

.back-right-lid {
  position: absolute;
  width: 50%;
  height: 100%;
  /* background: orange; */
  right: 0;
  -webkit-transform-origin: right center;
  -ms-transform-origin: right center;
  transform-origin: right center;
}

.back-left-lid {
  position: absolute;
  width: 50%;
  height: 100%;
  /* background: orange; */

  width: 100%;
  height: 50%;
  -webkit-transform-origin: center top;
  -ms-transform-origin: center top;
  transform-origin: center top;
}

.front-right-lid {
  position: absolute;
  width: 50%;
  height: 100%;
  /* background: orange; */
  bottom: 0;
  width: 100%;
  height: 50%;
  -webkit-transform-origin: center bottom;
  -ms-transform-origin: center bottom;
  transform-origin: center bottom;
}

@keyframes front-left-lid-open {
  from {
    -webkit-transform: rotate3d(0, 1, 0, 0deg);
    -ms-transform: rotate3d(0, 1, 0, 0deg);
    transform: rotate3d(0, 1, 0, 0deg);
  }
  50% {
    -webkit-transform: rotate3d(0, 1, 0, -190deg);
    -ms-transform: rotate3d(0, 1, 0, -190deg);
    transform: rotate3d(0, 1, 0, -190deg);
  }
  70% {
    -webkit-transform: rotate3d(0, 1, 0, -150deg);
    -ms-transform: rotate3d(0, 1, 0, -150deg);
    transform: rotate3d(0, 1, 0, -150deg);
  }
  to {
    -webkit-transform: rotate3d(0, 1, 0, -190deg);
    -ms-transform: rotate3d(0, 1, 0, -190deg);
    transform: rotate3d(0, 1, 0, -190deg);
  }
}

@keyframes back-right-lid-open {
  from {
    -webkit-transform: rotate3d(0, 1, 0, 0deg);
    -ms-transform: rotate3d(0, 1, 0, 0deg);
    transform: rotate3d(0, 1, 0, 0deg);
  }
  50% {
    -webkit-transform: rotate3d(0, 1, 0, 170deg);
    -ms-transform: rotate3d(0, 1, 0, 170deg);
    transform: rotate3d(0, 1, 0, 170deg);
  }
  70% {
    -webkit-transform: rotate3d(0, 1, 0, 100deg);
    -ms-transform: rotate3d(0, 1, 0, 100deg);
    transform: rotate3d(0, 1, 0, 100deg);
  }
  to {
    -webkit-transform: rotate3d(0, 1, 0, 140deg);
    -ms-transform: rotate3d(0, 1, 0, 140deg);
    transform: rotate3d(0, 1, 0, 140deg);
  }
}

@keyframes back-left-lid-open {
  from {
    -webkit-transform: rotate3d(1, 0, 0, 0deg);
    -ms-transform: rotate3d(1, 0, 0, 0deg);
    transform: rotate3d(1, 0, 0, 0deg);
  }
  50% {
    -webkit-transform: rotate3d(1, 0, 0, 190deg);
    -ms-transform: rotate3d(1, 0, 0, 190deg);
    transform: rotate3d(1, 0, 0, 190deg);
  }
  70% {
    -webkit-transform: rotate3d(1, 0, 0, 130deg);
    -ms-transform: rotate3d(1, 0, 0, 130deg);
    transform: rotate3d(1, 0, 0, 130deg);
  }
  to {
    -webkit-transform: rotate3d(1, 0, 0, 140deg);
    -ms-transform: rotate3d(1, 0, 0, 140deg);
    transform: rotate3d(1, 0, 0, 140deg);
  }
}

@keyframes front-right-lid-open {
  from {
    -webkit-transform: rotate3d(1, 0, 0, 0deg);
    -ms-transform: rotate3d(1, 0, 0, 0deg);
    transform: rotate3d(1, 0, 0, 0deg);
  }
  50% {
    -webkit-transform: rotate3d(1, 0, 0, -190deg);
    -ms-transform: rotate3d(1, 0, 0, -190deg);
    transform: rotate3d(1, 0, 0, -190deg);
  }
  70% {
    -webkit-transform: rotate3d(1, 0, 0, -150deg);
    -ms-transform: rotate3d(1, 0, 0, -150deg);
    transform: rotate3d(1, 0, 0, -150deg);
  }
  to {
    -webkit-transform: rotate3d(1, 0, 0, -190deg);
    -ms-transform: rotate3d(1, 0, 0, -190deg);
    transform: rotate3d(1, 0, 0, -190deg);
  }
}
