.game-nav {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
  padding: 10px;
  background-color: #f0f0f0;
}

.game-nav a {
  padding: 10px 20px;
  text-decoration: none;
  color: #333;
  font-weight: bold;
  border-radius: 5px;
  transition: all 0.3s;
}

.game-nav a:hover {
  background-color: #ddd;
}